.profile__description {
  margin-left: clamp(0px, (4.7vw - 30px), 30px);
  align-self: center;
  grid-area: desc;
  display: grid;
  grid-template-areas: "profile-n  profile-b" "profile-s  profile-s";
  white-space: nowrap;
  width: max-content;
  justify-items: start;
  max-width: clamp(282px, 50vw, 520px);
}
